.card {
    background: rgba(128, 128, 128, 0.199);
    display: inline-flex;
    width: 47.5%;
    margin: 1.25%;
    font-size: 1em;
    flex-direction: column;
    justify-content: center;
    
    border-radius: 25px;
}

.header {
    height: 50%;
    display: flex;
    flex-direction: row;
}

.header img {
    margin: 5%;
}

.header div.info {
    display: flex;
    margin: 5%;
    width: 100%;
    justify-content: center;
    flex-direction: column;

}

.footer {
    height: auto;
    margin: 2.5%;
    display: flex;
    flex-direction: row;
    justify-content: left;
}

.rewardDiv {
    
    margin: 2.5%;
    margin-top: 0;
    background-color: rgb(134, 134, 134);
    height: auto;
    width: 15%;
    padding: 2.5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    border-color: black;
    border-radius: 10%;
    border-width: 15%;
    border-style: solid;
    color: white;
    text-shadow: 0 0 10px black;
}

.rewardDiv * {
    margin: 0;

}

.rewardDiv h2 {
    margin-bottom: 15px;
}

.rewardDiv h5 {
    width: 100%;
    word-wrap: normal;
}

.card img {
    max-width: 40%;
    height: auto;
}