#cards {
    position:  relative;
    height: 100%;
}


#cards div#container {
    height: 100%;
}

#cards div#container div h2{
    margin: auto 0;
    
    height: auto;
}
#cards div#container div p {
    margin: auto 0;
    
}

#cards div#container div.content {
    height: 100%;
    display: flex;
    flex-direction: column;
    border: 5px solid black;
    
    
}

#cards div#container div.header {
    padding: 0 1.25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.searchCards {
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}

.searchCards *  {
    margin: 1.25% 2.5%;
}

.cardContainer {
    width: 97.5%;
    
    margin: 0 auto 1.25%;
    overflow-y: scroll;
}

.searchBar {
    display: flex;
    flex: column;
    justify-content: center;
}

form  div{
    display: block;
}
