.card {
    background: rgba(128, 128, 128, 0.199);
    display: inline-flex;
    
    margin: 1.25%;
    font-size: 1em;
    flex-direction: column;
    justify-content: center;

    border-radius: 25px;
}
.card div {
    text-align: center;
    align-content: center;
}


.card div.header {
    height: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.card div.header *:first-child {
    margin-left: 5%;
}
.card div.header *:last-child {
    margin-right: 5%;
}