#dash {
    position:  relative;
    height: 100%;
}


#dash div#container {
    height: 100%;
}

#dash div#container div h2{
    margin: auto 0;
    
    height: auto;
}
#dash div#container div p {
    margin: auto 0;
    
}

#dash div#container div.content {
    height: 100%;
}

#dash div#container div.header {
    padding: 0 1.25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}