

div.header {
    height: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
div.header *:first-child {
    margin-left: 5%;
}
div.header *:last-child {
    margin-right: 5%;
}


div.content {
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}


div.indivAccounts {
    height: 40%;
}
div.worth {
    margin: 0 5%;
    
    height: 25%;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

 div.account {
    margin: 0 5%;
    height: 25%;
    text-align: left;
    border-color: rgba(102, 102, 102, 0.435);
    border-width: .5px 0;
    border-style:solid none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}