

div.header {
    height: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
div.header *:first-child {
    margin-left: 5%;
}
div.header *:last-child {
    margin-right: 5%;
}


div.content {
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}